.foot-desc-txt {
    margin-top: 22px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 34px;
    color: #e3c08d;
  }
  
  .sns-context {
    margin-top: 17px;
    width: 137px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .foot-category-title {
    width: 106px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 34px;
    color: #c5c8c9;
  }
  
  .foot-category-link {
    margin-top: 20px;
    height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 170px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 34px;
  }
  
  .foot-category-link a {
    color: #e3c08d;
    text-decoration: none;
    cursor: pointer;
  }
  
  .find-us {
    display: flex;
    flex-direction: row;
  }
  
  .find-us span {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 34px;
    color: #c5c8c9;
  }
  
  .find-us div {
    margin-left: 10px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 34px;
    color: #e3c08d;
  }
  
  .copyright-txt {
    margin-top: 17px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 34px;
    color: #e3c08d;
  }
  
  .foot-category-title:after {
    display: block;
    content: "";
    border-bottom: solid 2px #d7b686;
    transform: scaleX(0);
    transition: transform 250ms ease-in-out;
    transform-origin: 0% 50%;
  }
  
  .foot-category-title:hover:after {
    transform: scaleX(1);
  }
  
/* Add hover effects for social icons */
.foot-social-icons img:hover {
  transform: scale(1.1);
  opacity: 0.8;
}

.foot-social-icons {
  gap: 15px !important;
}
  